import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import DollarGirl from "../Assets/images/dollar-gill-SoG-vKZ9bU4-unsplash.jpg";
import { faBookmark, faMinus, faPlus } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function Bag() {
  return (
    <div>
      <Header />
      <div class="bagSection PageFullHeight">
        <div class="container mb-5">
          <div class="row mt-5 mb-5 pb-5">
            <div class="col-md-8">
              <h2 class="font-weight-normal">Shopping Bag</h2>
              <div class="row">
                <div class="col-md-12">
                  <div class="containerCard">
                    <div class="row">
                      <div class="col-md-3">
                        <img src={DollarGirl} class="w-100 h-100" />
                      </div>
                      <div class="col-md-9">
                        <div class="row">
                          <div class="col-12">
                            <h5>fwerwerwerwer</h5>
                          </div>
                          <div class="col-6">
                            <p>Price</p>
                          </div>
                          <div class="col-6 text-right">
                            <p class="text-danger">222</p>
                          </div>
                          <div class="col-6">
                            <p>Material</p>
                          </div>
                          <div class="col-6 text-right">
                            <p>Cotton</p>
                          </div>
                          <div class="col-6">
                            <p>Size</p>
                          </div>
                          <div class="col-6 text-right">
                            <p>XL</p>
                          </div>
                          <div class="col-md-4 pt-2 text-right">
                            <div class="input-group">
                              <span class="input-group-prepend">
                                <button
                                  type="button"
                                  class="btn btn-outline-secondary btn-number"
                                  disabled="disabled"
                                  data-type="minus"
                                  data-field="quant[1]"
                                >
                                  <FontAwesomeIcon icon={faMinus} />
                                </button>
                              </span>
                              <input
                                type="text"
                                name="quant[1]"
                                class="form-control input-number"
                                value="1"
                                min="1"
                                max="10"
                              />
                              <span class="input-group-append">
                                <button
                                  type="button"
                                  class="btn btn-outline-secondary btn-number"
                                  data-type="plus"
                                  data-field="quant[1]"
                                >
                                  <FontAwesomeIcon icon={faPlus} />
                                </button>
                              </span>
                            </div>
                          </div>
                          <div class="col-md-4 pt-2 text-right">
                            <button class="btn  btn-outline-secondary w-100">
                              Add To Wishlist
                            </button>
                          </div>
                          <div class="col-md-4 pt-2 text-right">
                            <button class="btn  btn-outline-secondary w-100">
                              Remove Item
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4 border_left_gray">
              <div class="priceContainer">
                <div class="row">
                  <div class="col-md-12">
                    <h3 class="marginTop">
                      <FontAwesomeIcon icon={faBookmark} />
                      Price Details
                    </h3>
                    <hr class="bottomDottedLine" />
                  </div>
                  <div class="col-12 mb-3">
                    <h6 class="float-left">Sub Total</h6>
                    <p class="float-right">222</p>
                  </div>
                  <div class="col-12 mb-3">
                    <h6 class="float-left">Shipping</h6>
                    <p class="float-right">22</p>
                  </div>

                  <div class="col-12 mb-3">
                    <h6 class="float-left">
                      Total<span class="text-danger">(Incl. All Taxes)</span>
                    </h6>
                    <p class="float-right font-weight-bold">244</p>
                  </div>
                  <div class="col-12 text-center mt-2">
                    <button class="btn btn-danger w-100">PROCEED TO PAY</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Bag;
