import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "../Assets/css/style.css";
import RadharaniLogo from "../Assets/images/logo/radharani_logo transperent.png";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHeart,
  faSearch,
  faShoppingBag,
  faUserCircle,
} from "@fortawesome/free-solid-svg-icons";

import DropdownButton from "react-bootstrap/esm/DropdownButton";
import Dropdown from "react-bootstrap/Dropdown";

function Header() {
  return (
    <>
      <nav
        class="navbar navbar-light bg-white navbar-expand-lg fixedHeader paddind-left-custom"
        id="myNavbar"
      >
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#mainNav"
          aria-controls="mainNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="navbar-brand">
          <img src={RadharaniLogo} alt="Radha Rani Logo" class="widthLogo" />
        </div>
        <div class="collapse navbar-collapse responsive-nav" id="mainNav">
          <ul class="navbar-nav ml-0 nav-fill pt-3 ">
            <li class="nav-item px-1">
              <p class="nav-link nav_text_menu">
                Sarees <span class="sr-only">(current)</span>
              </p>
            </li>
            <li class="nav-item px-1">
              <Link to="/sale">
                <p class="nav-link nav_text_menu">Kurtis</p>
              </Link>
            </li>
            <li class="nav-item px-1">
              <Link to="/edit-profile">
                <p class="nav-link nav_text_menu">Gowns</p>
              </Link>
            </li>

            <li class="nav-item px-1">
              <Link to="/account-info">
                <p class="nav-link nav_text_menu">Lehengas</p>
              </Link>
            </li>
            <li class="nav-item px-1">
              <p class="nav-link nav_text_menu">Salwar Suits </p>
            </li>
            <li class="nav-item px-1">
              <Link to="/sale">
                <p class="nav-link nav_text_menu">Night Wear</p>
              </Link>
            </li>
            <li class="nav-item px-1">
              <Link to="/sale">
                <p class="nav-link nav_text_menu">Kids </p>
              </Link>
            </li>
            <li class="nav-item px-1">
              <Link to="/sale">
                <p class="nav-link nav_text_menu">Western</p>
              </Link>
            </li>
          </ul>
          <ul class="navbar-nav ml-auto nav-fill pt-4-custom">
            <li class="nav-item px-1">
              <a href="#" class="nav-link">
                <div class="input-group searchInputSize">
                  <span class="input-group-append searchInput">
                    <button
                      class="btn btn-outline-secondary border-0 pr-0"
                      type="button"
                    >
                      <FontAwesomeIcon icon={faSearch} />
                    </button>
                  </span>
                  <input
                    class="form-control py-2 border-0 searchInput"
                    placeholder="Search for products , brands and more"
                    type="search"
                    id="example-search-input"
                  />
                </div>
              </a>
            </li>

            <li class="nav-item px-1 dropdown" id="ProfileDropdownMain">
              {/* <a
                class="nav-link dropdown-toggle nav_text_menu"
                href="#"
                id="ProfileDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <FontAwesomeIcon
                  icon={faUserCircle}
                  className=" nav_icon user_icon_color profileDropDownHeading"
                />
                Profile
              </a> */}
              {/* <div
                class="dropdown-menu dropdown-menu-right megamenuStyle"
                aria-labelledby="ProfileDropdown"
              >
                <div class="d-md-flex align-items-start justify-content-start">
                  <div>
                    <div class="dropdown-header">Profile</div>
                    <a class="dropdown-item">Accounts</a>
                    <a class="dropdown-item">Edit Profile</a>
                    <a class="dropdown-item">Wishlist</a>
                    <a class="dropdown-item">Logout</a>
                  </div>
                </div>
              </div> */}
              <DropdownButton
                title={
                  <FontAwesomeIcon
                    icon={faUserCircle}
                    className=" nav_icon user_icon_color profileDropDownHeading"
                  />
                }
                className="dropDown mr-2 mt-2 "
              >
                <Dropdown.Item>
                  <Link to="/account-info" style={{ textDecoration: "none" }}>
                    Accounts
                  </Link>
                </Dropdown.Item>

                <Dropdown.Item>
                  <Link to="/edit-profile" style={{ textDecoration: "none" }}>
                    Edit Profile
                  </Link>
                </Dropdown.Item>

                <Dropdown.Item>
                  <Link to="/wishlist" style={{ textDecoration: "none" }}>
                    Wishlist
                  </Link>
                </Dropdown.Item>

                <Link to="/" style={{ textDecoration: "none" }}>
                  <Dropdown.Item>Logout</Dropdown.Item>
                </Link>
              </DropdownButton>
            </li>
            <li class="nav-item px-1">
              <Link to="/wishlist">
                <div class="nav-link">
                  <FontAwesomeIcon
                    icon={faHeart}
                    className=" nav_icon heart_icon_color"
                  />
                  <p class="nav_text_menu">Wishlist</p>
                </div>
              </Link>
            </li>
            <li class="nav-item px-1">
              <Link to="/bag">
                <div class="nav-link">
                  <FontAwesomeIcon
                    icon={faShoppingBag}
                    className=" nav_icon bag_icon_color"
                  />
                  <p class="nav_text_menu">Bag</p>
                </div>
              </Link>
            </li>
          </ul>
        </div>
      </nav>
    </>
  );
}

export default Header;
