import React from "react";

function Signup() {
  return (
    <div>
      <div className="container-fluid mt-5 mb-5">
        <div className="row pt-5 pb-5">
          <div className="col-md-4 col-sm-4"></div>
          <div className="col-md-4 col-sm-4 col-12 text-center">
            <h3 className="pb-3">Sign Up</h3>
            <p className="pb-4">Please fill in the information below: </p>

            <form action="">
              <div className="form-group">
                <input
                  type="text"
                  className="form-control inputHeight"
                  placeholder="First Name"
                  id="fname"
                />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  className="form-control inputHeight"
                  placeholder="Last Name"
                  id="lname"
                />
              </div>
              <div className="form-group">
                <input
                  type="email"
                  className="form-control inputHeight"
                  placeholder="Email"
                  id="email"
                />
              </div>
              <div className="form-group">
                <input
                  type="password"
                  className="form-control inputHeight"
                  placeholder="Password"
                  id="pwd"
                />
              </div>
              <button
                type="submit"
                className="btn btn-danger w-100 inputHeight"
              >
                Sign Up
              </button>
            </form>
            <p className="pt-3 text-muted">
              Already have an account?
              <a href="login.html" className="text-dark">
                Log In{" "}
              </a>
            </p>
          </div>
          <div className="col-md-4 col-sm-4"></div>
        </div>
      </div>
    </div>
  );
}

export default Signup;
