import React from "react";
import Header from "./Header";
import Footer from "./Footer";
function AccountInfo() {
  return (
    <div>
      <Header />
      <div class="container mt-5 mb-5 pt-5 pb-1">
        <div class="row mt-5 mb-5 pb-5">
          <div class="col-md-6 text-left">
            <h4>My account</h4>
            <p class="text-muted mt-3">
              {" "}
              <small> Welcome Back Pooja</small>
            </p>
          </div>
          <div class="col-md-6 text-right">
            <button class="btn btn-danger  rounded-0">Log Out</button>
          </div>
        </div>

        <div class="row mt-5 mb-5 pt-3">
          <div class="col-md-7 text-left">
            <h4>My Orders</h4>
            <hr />
            <p class="text-muted mt-3">
              <small>No Orders Placed Yet</small>
            </p>
          </div>
          <div class="col-md-1"></div>
          <div class="col-md-4 text-right">
            <p class="text-muted">No Address</p>
            <hr />
            <p class="text-muted mt-3">
              {" "}
              <small>No addresses are currently saved</small>
            </p>
            <button
              class="btn btn-danger  rounded-0"
              data-toggle="modal"
              data-target="#myModalAddress"
            >
              Manage Addresses
            </button>
          </div>
        </div>
      </div>

      <div class="modal" id="myModalAddress">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title">My Address</h4>
              <button type="button" class="close" data-dismiss="modal">
                &times;
              </button>
            </div>

            <div class="modal-body">
              <div class="form-group">
                <input
                  type="text"
                  class="form-control inputHeight"
                  placeholder="First Name"
                  id="fname"
                />
              </div>
              <div class="form-group">
                <input
                  type="text"
                  class="form-control inputHeight"
                  placeholder="Last Name"
                  id="fname"
                />
              </div>
              <div class="form-group">
                <input
                  type="text"
                  class="form-control inputHeight"
                  placeholder="Phone"
                  id="fname"
                />
              </div>
              <div class="form-group">
                <input
                  type="text"
                  class="form-control inputHeight"
                  placeholder="Address"
                  id="fname"
                />
              </div>
              <div class="form-group">
                <input
                  type="text"
                  class="form-control inputHeight"
                  placeholder="City"
                  id="fname"
                />
              </div>
              <div class="form-group">
                <select
                  type="text"
                  class="form-control inputHeight"
                  placeholder=""
                  id="fname"
                >
                  <option value="" selected disabled>
                    State
                  </option>
                </select>
              </div>
              <div class="form-group">
                <div class="row">
                  <div class="col-md-6">
                    <select
                      type="text"
                      class="form-control inputHeight"
                      placeholder=""
                      id="fname"
                    >
                      <option value="" selected disabled>
                        +IN
                      </option>
                    </select>
                  </div>
                  <div class="col-md-6">
                    <input
                      type="text"
                      class="form-control inputHeight"
                      placeholder="Zip Code"
                      id="fname"
                    />
                  </div>
                </div>
              </div>
            </div>

            <div class="modal-footer text-center">
              <button type="submit" class="btn btn-danger" data-dismiss="modal">
                Add My Address
              </button>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default AccountInfo;
