import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import GirlsProfile from "../Assets/images/girl.jpg";
import { faPencilAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function EditProfile() {
  return (
    <div>
      <Header />
      <div class="AllProductSection">
        <div class="container">
          <div class="row">
            <div class="col-2"></div>
            <div class="col-8 mb-5">
              <h4 class="font-weight-normal mt-5">Profile</h4>
              <div class="profileConatiner">
                <div class="profileImageBox position-relative">
                  <img
                    src={GirlsProfile}
                    class="w-100 h-100 rounded-circle"
                    alt=""
                  />
                  <FontAwesomeIcon
                    icon={faPencilAlt}
                    className="change w-25 h-25"
                  />
                </div>
              </div>
              <h4 class="font-weight-normal mt-5">Basic Info</h4>
              <div class="profileConatiner pt-4 pb-4">
                <div class="row">
                  <div class="col-10">
                    <input
                      type="text"
                      class="w-100 border-0 pl-2 bg-input"
                      placeholder="Mobile Number"
                    />
                  </div>
                  <div class="col-2 text-right">
                    <button class="btn btn-success text-white">Change </button>
                  </div>
                  <div class="col-12 mt-4">
                    <input
                      type="text"
                      class="w-100 border-0 pl-2 bg-input"
                      placeholder="Name"
                    />
                  </div>
                  <div class="col-12  mt-4">
                    <input
                      type="text"
                      class="w-100 border-0 pl-2 bg-input"
                      placeholder="Email"
                    />
                  </div>
                  <div class="col-12  mt-4">
                    <button class="w-100 btn btn-success text-white">
                      Save Info
                    </button>
                  </div>
                </div>
              </div>
              <h4 class="font-weight-normal mt-5">Change Password</h4>
              <div class="profileConatiner pt-4 pb-4">
                <div class="row">
                  <div class="col-12">
                    <input
                      type="text"
                      class="w-100 border-0 pl-2 bg-input"
                      placeholder="Current Password"
                    />
                  </div>
                  <div class="col-12 mt-4">
                    <input
                      type="text"
                      class="w-100 border-0 pl-2 bg-input"
                      placeholder="New Password"
                    />
                  </div>
                  <div class="col-12  mt-4">
                    <input
                      type="text"
                      class="w-100 border-0 pl-2 bg-input"
                      placeholder="Confirm Password"
                    />
                  </div>
                  <div class="col-12  mt-4">
                    <button class="w-100 btn btn-success text-white">
                      Save New Password
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default EditProfile;
