import React from "react";

function Login() {
  return (
    <div>
      <div class="container-fluid mt-5 mb-5">
        <div class="row pt-5 pb-5">
          <div class="col-md-4 col-sm-4"></div>
          <div class="col-md-4 col-sm-4 col-12 text-center">
            <h3 class="pb-3">Log In</h3>
            <p class="pb-4">Please enter your e-mail and password: </p>

            <form action="">
              <div class="form-group">
                <input
                  type="email"
                  class="form-control inputHeight"
                  placeholder="Email"
                  id="email"
                />
              </div>
              <div class="form-group">
                <input
                  type="password"
                  class="form-control inputHeight"
                  placeholder="Password"
                  id="pwd"
                />
              </div>
              <div class="col-md-12 text-right p-0 mb-1">
                <a href="forgot_password.html">
                  <small class="text-muted">Forgot Password?</small>
                </a>
              </div>
              <button type="submit" class="btn btn-danger w-100 inputHeight">
                LOG IN
              </button>
            </form>
            <p class="pt-3 text-muted">
              Don't have an account?
              <a href="signup.html" class="text-dark">
                Create one{" "}
              </a>
            </p>
          </div>
          <div class="col-md-4 col-sm-4"></div>
        </div>
      </div>
    </div>
  );
}

export default Login;
